<template>
  <div class="like">
    <van-nav-bar title="我的收藏" left-text="返回" left-arrow :fixed="true" @click-left="returnLast" />
    <div class="like-bg"></div>
    <div class="like-box">
      <div class="product-box clearfix" v-if="likeData.length > 0">
        <div
          class="product-item fl"
          :class="[index % 2 == 0 ? 'p-left' : 'p-right']"
          v-for="(item, index) in likeData"
          :key="index"
        >
          <div class="p-img" @click="viewProductInfo(item.pid)">
            <img class="auto-img" :src="item.smallImg" alt />
          </div>
          <div class="pro-name one-text">{{item.name}}</div>
          <div class="clearfix">
            <div class="pro-price fl">￥{{item.price}}</div>
            <div class="pro-remove fr" @click="removeLike(item.pid, index)">
              <van-icon name="delete" />
            </div>
          </div>
        </div>
      </div>
      <div v-else>
        <van-empty description="收藏商品空空如也，去逛一逛!">
          <van-button round color="#0C34BA" class="bottom-button" @click="goMenu">
            去逛一逛
          </van-button>
        </van-empty>
      </div>
    </div>
  </div>
</template>

<script>
import {utils} from '../assets/js/utils'
export default {
    data(){
        return {
            likeData: [],
        }
    },

    created(){
        this.getAllLikeData();
    },

    methods: {
        goMenu(){
            return this.$router.push({name:'Menu'})
        },

        returnLast(){
            this.$router.go(-1);
        },

        //获取我的收藏商品数据
        getAllLikeData(){
            let tokenString = localStorage.getItem('CSTK');
            if (!tokenString) {
                return this.$router.push({name:'Login'});
            }

            this.$toast.loading({
                message: "加载中...",
                forbidClick: true,
                duration: 0,
                loadingType: "spinner",
              });
            
            this.axios({
                methods: 'GET',
                url: '/findAllLike',
                params:{
                    appkey:this.appkey,
                    tokenString,
                },
            }).then(result =>{
                this.$toast.clear();
                if (result.data.code == 2000) {
                    this.likeData = result.data.result;
                }
            }).catch(err =>{
                this.$toast.clear();
                
            })
        },

        //查看商品详情
        viewProductInfo(pid){
            this.$router.push({name:'Detail',query:{pid}});
        },

        //删除商品数据
        removeLike(pid,index) {
            // 
            let tokenString = localStorage.getItem('CSTK');
            if (!tokenString) {
                return this.$router.push({name:'Login'});
            }

            let data = utils.queryString({
                appkey:this.appkey,
                tokenString,
                pid
            })

            this.$toast.loading({
                message: '加载中...',
                forbidClick: true,
                duration: 0,
                loadingType: 'spinner'
            })

            this.axios({
                method: 'POST',
                url: '/notlike',
                data
            }).then(result =>{
                this.$toast.clear();
                
                if (result.data.code == 700) {
                    return this.$router.push({name:'Login'});
                }
                
                if (result.data.code == 900) {
                   this.likeData.splice(index, 1);
                   this.$toast('删除商品成功') 
                }else{
                    this.$toast('删除商品失败')
                }
                
            }).catch(err =>{
                this.$toast.clear();
                
            })
        }
    }
}
</script>

<style lang="less" scoped>
.like{
  padding-top: 46px;
  /deep/ .van-nav-bar__text{
    color: #0C34BA;
  }

  /deep/ .van-nav-bar .van-icon{
    color: #0C34BA;
  }

  .like-bg{
    height: 100px;
    background-color: #0C34BA;
  }

  .like-box{
    margin: -30px 10px 0;
    border-top-left-radius: 10px;
    border-top-right-radius: 10px;
    overflow: hidden;
    background-color: #fff;
    padding: 10px;
  }

  .product-item{
    width: calc(~"50% - 25px");
    padding: 10px;
    background-color: #f5f5f5;
    border-radius: 10px;
    margin-bottom: 10px;
  }
  .p-left{
    margin-right: 5px;
  }
  .p-right{
    margin-left: 5px;
  }
  .p-img{
    border-radius: 10px;
    overflow: hidden;
  }
  .pro-name{
    font-size: 16px;
    font-weight: bold;
    padding: 10px 0;
    color: #444;
  }
  .en-pro-name{
    color: #666;
    font-size: 14px;
    width: calc(~"100% - 50px");
  }
  .pro-price{
    font-size: 14px;
    // width: 50px;
    color: #0C34BA;
    font-weight: bold;
    line-height: 20px;
  }

  .pro-remove{
    width: 20px;
    height: 20px;
    font-size: 20px;
    color: #999;
  } 
}
</style>